.navigation {
    height: 70px;
    background-color: #262626;
}

.nav-container {
    max-width: 1000px;
    margin: 0 auto;
    /* set to center */
}

.nav-r {
    position: absolute;
    float: left;
    padding-left: 20px;
    line-height: 70px;
    /* make center to bar */

    color: white;
}

.nav-r a,
.nav-r a:visited {
    color: #ffffff;
    text-decoration: none;
}


.nav-l {
    float: right;
}

nav ul {
    list-style: none;
    /* margin: 0;
    padding: 0; */
}

nav ul li {
    /* make it rows horizontal */
    float: left;
    position: relative;

    display: block;
    /* make it separately */

    padding: 0 20px;
    line-height: 70px;
}

nav ul li a,
nav ul li a:visited {
    color: #fff;
    text-decoration: none;
}

/* Mobile navigation */
.nav-mobile {
    display: none;
    position: absolute;
    top: 0;
    right: 0;
    background: #262626;
    height: 70px;
    width: 70px;
}

.sticky {
    position: sticky;
    top: 0;
}

#nav-toggle {
    position: absolute;
    left: 18px;
    top: 22px;
    cursor: pointer;
    padding: 10px 35px 16px 0px;
}

#nav-toggle span,
#nav-toggle span:before,
#nav-toggle span:after {
    cursor: pointer;
    border-radius: 1px;
    height: 5px;
    width: 35px;
    background: #ffffff;
    position: absolute;
    display: block;
    content: "";
    transition: all 300ms ease-in-out;
}

#nav-toggle span:before {
    top: -10px;
}

#nav-toggle span:after {
    bottom: -10px;
}

#nav-toggle.active span {
    background-color: transparent;
}

#nav-toggle.active span:before,
#nav-toggle.active span:after {
    top: 0;
}

#nav-toggle.active span:before {
    transform: rotate(45deg);
}

#nav-toggle.active span:after {
    transform: rotate(-45deg);
}

@media only screen and (max-width: 798px) {
    .nav-mobile {
        display: block;
    }

    nav {
        width: 100%;
        padding: 50px 0 15px;
        /* background-color: #262626; */
    }

    nav ul {
        display: none;
    }

    nav ul li {
        float: none;
        padding: 15px;
        line-height: 20px;
    }
}

@media screen and (min-width: 799px) {
    .nav-list {
        display: block !important;
    }
}