.list {
    height: 50px;
    margin: 20px auto;
    text-align: left;
    background-color: black;
    border-radius: 8px;
    padding-left: 16px;
    max-width: 600px;
  
}
.list p {
    padding: 10px;
    position: relative;
    color: white;
}
.list p input {
    background-color: transparent;
    border:0;
    color:#fff;
    font-size:18px;
}
.list p input:focus {
    outline: none;
}
.list p span {
    position: absolute;
    right: 10px;
    cursor: pointer;
}

.faicons {
    color: white;
    margin:0 5px;
    font-size: 15px;
}