.todo_container {
   margin: 0 auto;
    max-width: 400px;
}
.todo_add {
    display: flex;
    flex-direction: row;
    align-items: baseline;
    justify-content: center;

}

.todo_add input {
    height: 44px;
    width: 100%;
    margin-right: 20px;
    padding-left: 16px;
}

.button_todo {
    margin-top: 10px;
    margin-bottom: 20px;
    color: #fff;
    background-color: #53a7ea;
    border: 1px solid #1D9AF2;
    border-radius: 4px;
    padding: 0 20px;
    cursor: pointer;
    height: 44px;
    font-size: 14px;
    transition: all 0.2s ease-in-out;
    outline: none;
}

.button_todo:hover {
    box-shadow: 1px 1px #53a7ea, 2px 2px #53a7ea, 3px 3px #53a7ea;
    transform: translate(-3px);
}