

.container_joke {
    padding: 10px;
    display: flex;
    align-items: flex-start;
    justify-content: center;
}

.card {
    text-align: center;
    width: fit-content;
    /* margin: 0 auto; */
    /* set to center */

    border-radius: 5px;
    padding: 5px 30px;
    box-shadow: 0px 5px 10px #aaa;
}
.card h1 {
    line-height: 5;
}
.card p {
    line-height: 2;
}
.card-t {
   
}

.button_joke {
    margin-top: 10px;
    margin-bottom: 20px;
    color: #fff;
    background-color: #53a7ea;
    border: 1px solid #1D9AF2;
    border-radius: 4px;
    padding: 0 15px;
    cursor: pointer;
    height: 32px;
    font-size: 14px;
    transition: all 0.2s ease-in-out;
    outline: none;
    display: block;
    width: 100%;
}

.button_joke:hover {
    box-shadow: 1px 1px #53a7ea, 2px 2px #53a7ea, 3px 3px #53a7ea;
    transform: translate(-3px);
}