.slider {
    /* border: 1px solid red; */
    width: 500vw;
    height: 100vh;
    background: black;
    box-sizing: border-box;
    margin: 0;
    padding: 0;

    overflow: hidden;

    position: relative;

    /* make it horizontal */
    display: flex;
    align-items: center;
    justify-content: center;
}

.slide {
    /* border: 1px solid blue; */
    width: 100%;
    height: 80%;

    overflow: hidden;

    transition: .5s;
}

.slide_btn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 5vw;
    min-width: 20px;
    height: 80%;
    background: transparent;
    border: none;
    outline: none;
    transition: 0.5s;
    cursor: pointer;
}

.slide_btn:hover {
    background: rgba(255, 255, 255, 0.1);
}

#goLeft {
    left: 0;
}

#goRight {
    left: 94vw;
}
